import { Memoize, isEmpty, parseIfString } from '@springbokagency/stencil-base';
import { Component, Host, h, Prop, Listen } from '@stencil/core';
import { BsTitle } from '../../atoms/title/title';

/**
 * The Accordion component.
 */
@Component({
  tag: 'bs-accordion',
  styleUrl: 'accordion.scss',
  shadow: true,
})
export class BsAccordion {
  /**
   * Section's title.
   */
  @Prop() mainTitle?: BsTitle | string;

  @Memoize('mainTitle')
  get parsedMainTitle(): BsTitle {
    return parseIfString(this.mainTitle);
  }

  // eslint-disable-next-line class-methods-use-this
  @Listen('bsAccordionStateAction')
  bsAccordionStateHandler(event: CustomEvent<boolean>) {
    const target = event.target as HTMLBsAccordionItemElement;

    target.active = !target.active;
  }

  render() {
    return (
      <Host>
        <section class="accordion-wrapper">
          {/* Main title */}
          {!isEmpty(this.parsedMainTitle) && !isEmpty(this.parsedMainTitle.text) && (
            <bs-title
              variant={this.parsedMainTitle.variant}
              color={this.parsedMainTitle.color}
              tag={this.parsedMainTitle.tag}
              text={this.parsedMainTitle.text}
              size={this.parsedMainTitle.size}
            />
          )}
          <ul class="accordion" aria-label="Accordion Control Group">
            <slot name="accordion-item" />
          </ul>
        </section>
      </Host>
    );
  }
}
